import { ActionOrTriggerListTypeEnum, IActionOrTriggerListPageParams } from "@app/static";
import { getTriggerOrActionListPageParams, ProjectMemberType, triggerTimeoutUtils } from "@droid/project-interface";
import { projectEventUtils } from "@droid/project-interface";
import { throwTraceError } from "@fanfanlo";
import { createActionOrTriggerButtonRender, IActionOrTriggerItem } from "../../../../../../components/trigger-list-action-list-common/ITriggerList-IActionList-common.intf";

export const timeoutTriggerItem:IActionOrTriggerItem = {
    id: "timeout-trigger-item-ce8c",
    i18nNs: "homepage/components/page/trigger-list/groups/time/timeout/content",
    nameKey: "content.name",
    group: "timeoutTriggerItem.group",
    descriptionKey: "content.description",
    type: "trigger",
    LinkRender: createActionOrTriggerButtonRender(onSelect)
}

function onSelect({query, item}:{query:IActionOrTriggerListPageParams, item:IActionOrTriggerItem}){
    const res = getTriggerOrActionListPageParams(query)
    switch(query.type){
        case ActionOrTriggerListTypeEnum.addAction:
            const trigger = triggerTimeoutUtils.createDefultData()
            if(!res.data.triggerGroup){
                return throwTraceError('triggerGroup is not found', query)
            }
            projectEventUtils.dispatchEditEvent(projectEventUtils.EditType.add, {
                projectMemberType: ProjectMemberType.triggerGroup,
                data:trigger,
                projectInfo:res.data
            })
            // res.data.triggerGroup.list.push(trigger)
            // router.back()
            window.history.back()
            break;
        case ActionOrTriggerListTypeEnum.updateAction:
            
            break;
    }
}