import { IActionOrTriggerListPageParams } from '@app/static';
import { AppShell } from '@app/ui';
import { useTranslation } from '@fanfanlo';
import { triggerListGroups } from '@src/components/page/trigger-list/trigger.data';
import { TriggerListOrActionListUI } from '@src/components/trigger-list-action-list-common/TriggerListOrActionListUI';
import { useRouter } from 'next/router';
// import { useTranslation } from 'react-i18next';

export default function TriggerList() {
    const router = useRouter()
    const { t } = useTranslation("homepage/pages/trigger-list/content")
    return (
        <AppShell>
            <TriggerListOrActionListUI groups={triggerListGroups} 
            titleConf={{title:t("content.title")}}
            
            title={t("content.title")} actionListParams={router.query as IActionOrTriggerListPageParams} />
        </AppShell>
    )
}   